:root {
    --category-details-background: #{$white};
}

.CategoryDetails {
    display: grid;
    grid-template-columns: auto minmax(100px, 160px);
    grid-gap: 12px;
    background-color: var(--category-details-background);
    margin-block-end: 12px;

    @include mobile {
        grid-template-columns: auto minmax(100px, 210px);
        grid-gap: 14px;
        margin-block-end: 14px;
        display: none;
    }

    &-Picture {
        mix-blend-mode: multiply;
        height: 72px;
        background: none;
    }

    &-Heading {
        text-transform: none;
        margin-block-end: 6px;
        margin-block-start: 0;

        @include mobile {
            margin-block-end: 7px;
        }
    }

    &-Description {
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-size: 14px;
        font-weight: 400;

        li {
            &::before {
                position: relative;
                margin-inline-start: 7px;
                margin-inline-end: 7px;
                inset-block-start: -1px;
            }
        }

        @include mobile {
            padding: 28px;
        }
    }
}
